@-webkit-keyframes slide
{
    0%   {  transition: opacity .75s ease-in-out , transform 0.75s , box-shadow 0.75s ;}
    100% {  opacity: 8;
  z-index:99;
  box-shadow: 5px 10px #888888;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  transition: opacity .75s ease-in-out,   transform 0.75s, box-shadow 0.75s;
  transform: translate(0%, -30%);}
}​

@-moz-keyframes slide
{
   0%   {  transition: opacity .75s ease-in-out , transform 0.75s , box-shadow 0.75s ;}
    100% {  opacity: 8;
  z-index:99;
  box-shadow: 5px 10px #888888;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  transition: opacity .75s ease-in-out,   transform 0.75s, box-shadow 0.75s;
  transform: translate(0%, -30%);}
}

@keyframes slide
{
    0%   {  transition: opacity .75s ease-in-out , transform 0.75s , box-shadow 0.75s ; 				-webkit-transform: rotate(0deg);}
    50% {  opacity: 8;   height: 65px;
  z-index:99;
  /* box-shadow: 5px 10px #888888; */
  /* box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5); */
  transition: opacity .75s ease-in-out,   transform 0.75s, box-shadow 0.75s;
  transform: translate(0%, -30%);-webkit-transform: rotate(180deg);}
   100%   {  transition: opacity .75s ease-in-out , transform 0.75s , box-shadow 0.75s ;-webkit-transform: rotate(359deg);}  
}

